.read-mmore{
	display: inline-block;
	padding: 10px 32px;
	font-size: 15px;
	font-weight: 700;
	background-color: $c-gy;
	border-radius: 3px;
	color: #fff;
	box-shadow: 0 0 3px #333333;
	border: none;
	cursor: pointer;
	&:hover{
		text-decoration: none;
		background-color: darken( $c-gy, 10 );
		box-shadow: none;
	}
}
.storry{
	&__butt{
		display: inline-block;
		padding: 11px 30px;
		border: 3px solid $c-blu;
		color: $c-blu;
		font-weight: bold;
		&:hover{
			text-decoration: none;
			background-color: $c-blu;
			color: #fff;
		}
	}
}


.overview{
	.read-mmore{
		color: #FDAA00;
		background-color: transparent;
		border: 2px solid #FDAA00; 
		padding: 8px 32px;
		text-decoration: none;
		box-shadow: none;
		&:hover{
			background-color: $c-blu;
			color: #fff;
		} 
	}
}

.butt{
	&.round-butt{
		color: $c-roz;
		font-size: 22px;
		font-weight: 700;
		display: inline-block;
		text-decoration: none;
		border: 3px solid $c-roz;
		padding: 5px 20px ;
		border-radius: 30px;
		min-width: 243px;
		text-align: center;
		vertical-align: middle;
		margin: 15px 5px;
		&.prev{
			&:before{
				content: "";
				width: 22px;
				height: 20px;
				display: inline-block;
				margin-top: -5px;
				vertical-align: middle;
				background:  url('../img/how_it_works/arr.png') center no-repeat;
				margin-right: 10px;
			}

		}
		&.next{
			&:after{
				content: "";
				width: 22px;
				height: 20px;
				margin-left: 10px;
				vertical-align: middle;
				display: inline-block;
				margin-top: -5px;
				background:  url('../img/how_it_works/arr-next.png') center no-repeat;
			}
		}
		&:hover{
			color: #fff;
			background-color: $c-roz;
		}
	}	
}
