.miless{
	&__head{
		text-align: center;
		font-size: 30px;
		font-weight: 800;
		margin-bottom: 20px;
	}
	.odd{
		background-color: #D5D4FB;
	}
	.even{
		background-color: #E6E6FD;
	}
	&__it{

		display: flex;
		align-items: center;
		border-radius: 20px;
		margin-bottom: 15px;
		
	}
	&__blck{
		padding: 20px 30px;
	}
	.blck-1{
		flex: 1;
		text-align: center;
		font-size: 30px;
		font-family: Lato;
		font-weight: 700;
		color: $c-blu;
	}
	.blck-2{
		flex: 5;
	}
}//.miless