body{
	font-family: Raleway, Helvetica, sans-serif;
	font-weight: 500;
	font-size: 18px;
	color: $c-gy;
	line-height: 1.5;
	background-color: #eeeeee;

} 

.cont1{
	max-width: 1140px;
	margin: 0 auto;
}

.cont2{
	max-width: 1140px;
	margin: 0 auto;	
}

.header{
	background: url('../img/header-bg.jpg') center top no-repeat;
	background-size: cover;
	min-height: 543px;
	&__blck{
		max-width: 700px;
	}
	&__titl{
		font-weight: bold;
	  background: -webkit-linear-gradient( left, #FE6CA3 0%, #484848 100% );
	  -webkit-background-clip: text;
	  -webkit-text-fill-color: transparent;
		margin-bottom: 45px;
	}
	&__logo{
		margin-bottom: 20px;
	}
	&__txt{
		font-size: 14px;
		p{
			margin-bottom: 30px;
		}
	}
}//.header

.info{
	color: #9A9A9A;
	&__title{
		color: #FE6CA3;
		font-weight: 800;
		margin-bottom: 45px;
	}
	&__blck{
		text-align: center;
		max-width: 700px;
		margin: 0 auto;
	}
}//.info


.colored{
	&-1{
		background-color: #EBFEFF;
		.colored__head{
			color: #544EF0;
		}
		.colored__txt{
			color: #7572CA;
		}
	}
	&-2{
		background-color: #F4FFED;
		.colored__head{
			color: #55D300;
		}
		.colored__txt{
			color: #82C258;
			p{
				&:before{
					background-color: #82C258;
				}
			}
		}	
	}
	&__head{
		font-size: 24px;
		font-weight: 800;
		line-height: 1.1;
		margin-top: 5px;
	}
	&__blck{
		width: 50%;
		float: left;
	}
	&__txt{
		p{
			margin-bottom: 10px;
			padding-left: 40px;
			position: relative;
			&:before{
				content: "";
				width: 4px;
				height: 4px;
				background-color: #544EF0;
				display: block;
				position: absolute;
				left: 0;
				top: 12px;
				border-radius: 50%;
			}	
			&:last-child{
				margin-bottom: 0;		
			}	
		}
	}
}

.process{
	text-align: center;
	padding-top: 100px;
	&__title{
		color: #FE6CA3;
	}
	&__list{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-top: 70px;
	}
	&__blck{
		flex-basis: 32.5%;
		min-height: 323px;
		color: #fff;
		padding: 30px;
		margin-bottom: 30px;
	}
	&__head{
		font-size: 24px;
		font-weight: 800;
		margin: 50px 0 50px;
	}
	.blck-1{
		background: url('../img/process1.jpg') right bottom;
		background-size: cover;
	}
	.blck-2{
		background: url('../img/process2.jpg') right bottom;
		background-size: cover;
	}
	.blck-3{
		background: url('../img/process3.jpg') right bottom;
		background-size: cover;
	}
}//.process

.facts{
	color: #fff;
	background: url('../img/facts-bg.jpg') center no-repeat;
	background-size: cover;
	&__blck{
		max-width: 50%;
		float: right;
		.colored__txt{
			p{
				&:before{
					background-color: #fff;
				}	
			}
		}
	}
	&__descr{
		font-size: 24px;
		font-weight: 800;
		margin: 40px 0;
	}
}//.facts

.physicians{
	&__title{
		font-weight: bold;
	  background: -webkit-linear-gradient( left, #AE587B 0%, #B3B496 100% );
	  -webkit-background-clip: text;
	  -webkit-text-fill-color: transparent;
		margin-bottom: 45px;
	}
	.blck1{
		max-width: 60%;
		margin: 0 auto;
		text-align: center;
	}
	&__descr{
		font-size: 14px;
	}
	.blck2{
		width: 50%;
		float: left;
		padding: 50px 60px;
	}
	&__txt{
		text-align: justify;
		color: #5C59C2;
		padding: 0 40px;
	}
	&__img{
		margin: 30px auto;
	}
}//.physicians

.office{
	display: flex;
	color: #50333a;
	&__blck{
		flex-basis: 50%;
	}
	.blck-1{
		background: rgba(209,119,150,1);
	  background: linear-gradient(to right, rgba(209,119,150,1) 0%, rgba(228,189,174,1) 100%);
	}
	&__wrapp{
		max-width: 570px;
		float: right;
		padding: 70px 40px 70px 0;
	}
	&__descr{
		font-family: Lato;
		font-weight: bold;
		margin: 25px 0px;
		b{
			color: #cc2671;
		}
	}
	&__txt{
		font-size: 14px;
	}
	&__tbl{
		border: 1px solid #fff;
		font-family: Lato;
		color: #000;
		font-weight: bold;
		display: flex;
		margin: 50px 0 20px;
		.celll{
			padding: 25px 10px 25px 20px;
			display: flex;
			align-items: center;	
		}
		.cell-1{
			// width: 60%;
			flex-basis: 60%;
		}
		.cell-2{
			// width: 40%;
			flex-basis: 40%;
		}
	}
	&__ico{
		display: inline-block;
		width: 100%;
		max-width: 50px;
		height: 50px;
		border: 3px solid #fff;
		border-radius: 50px;
		margin-right: 10px;
	}
}//.office

.footer{
	padding: 30px 0;
	&__inner{
		// display: flex;
		vertical-align: middle;
	}
	img{ 
		margin-bottom: 0;
		display: inline-block;
		vertical-align: middle;
	}
	&__info{
		font-family: Lato;
		font-weight: bold; 
		background-color: #FE6CA3;
		color: #fff;
		display: inline-block;
		vertical-align: middle;
		margin: 0 15px;
		padding: 4px 12px;
		border-radius: 11px;
	}
}//.footer

#map{
	min-height: 300px;
}




