
.topp{
	min-height: 794px;
	&__nav{
		display: flex;
		justify-content: space-between;
		padding: 15px 0;
		align-items: center;
		border-bottom: 1px solid #fff;
		margin-bottom: 50px;
		a{
			color: #fff;
		}
		&-blck{
			
		}
		.blck-2{
			width: 100%;
			max-width: 250px;
			text-align: right;
		}
		&-butt{
			display: inline-block;
			vertical-align: middle;
			cursor: pointer;
			margin-left: 15px;
		}
		&-phone{
			font-weight: 700;
			font-family: 'ProximaNova';
			position: relative;
			padding-left: 20px;
			&:before{
				content: "";
				width: 15px;
				height: 15px;
				background: url('../img/landing/phone-wh.png') center no-repeat;
				display: block;
				position: absolute;
				left: 0;
				top: 3px;
			}
		}
	}
	&__info{
		max-width: 1000px;
		margin: 0 auto;
		font-size: 14px;
		margin-bottom: 10px;

		&-titl{
			margin-bottom: 15px;
		}
	}
	&__list{
		font-size: 0;
		text-align: center;
		&-it{
			position: relative;
			display: inline-block;
			padding: 20px 5px;
			opacity: 0.4;
			transition: all .3s ease;
			&.akktiff{
				opacity: 1;
				.topp__list-info{
					display: block;
				}
			}
			&:hover{
				opacity: 1;
			}
		}
		&-person{
			cursor: pointer;				
		}
		&-info{
			background-color: #fff;
			width: 400px;
			display: none;
			position: absolute;
			top: 180px;
			border-radius: 6px;
		  left: 50%;
		  transform: translateX(-50%);
		  box-shadow: 0 0 10px #ccc;
		  padding: 20px;
		  font-size: 14px;
		  &:before{
				content: "";
				width: 20px;
				height: 20px;
				background-color: #fff;
				position: absolute;
				top: -10px;
				left: 50%;
				margin-left: -10px;
				transform: rotate(45deg);
			}
		}
		&-wrap{
			display: flex;
			align-items: center;
		}
		&-blck1{
			flex-basis: 25%;
			font-size: 18px;
			line-height: 1;
			b{
				font-size: 36px;
				color: $c-roz;
				font-size: 36px;

			}
		}
		&-blck2{
			flex-basis: 75%;
			border-left: 2px solid $c-roz;
			padding-left: 15px;
			font-size: 14px;
			text-align: left;
			b{
				font-family: ProximaNova;
				color: $c-roz;
				font-size: 18px;
				display: inline-block;
				margin-bottom: 10px;
			}
		}
	}//.list

}//.topp

.howw{
	text-align: center;
	background-color: #FBFCFC;
	&__title{
		color: $c-roz;
		a{
			color: $c-roz;
		}
	}
	&__group{
		display: flex;
		justify-content: space-between;
	}
	&__it{
		flex-basis: 24%;
		border: 1px solid #E5EBF0;
		padding: 25px 15px;
		background-color: #F7FAFA;
		box-shadow: 0 0 10px #999;
		position: relative;
		padding-bottom: 50px;
		.read-mmore{
			position: absolute;
			left: 50%;
			max-width: 150px;
			width: 100%;
			transform: translateX(-50%);
			bottom: 15px;
		}
	}
	&__img{
		display: flex;
		justify-content: center;
 		align-items: center;
		min-height: 110px;	
	}
	&__head{
		font-size: 22px;
		font-weight: 800;
		color: $c-roz;
	}
	&__txt{
		font-size: 14px;
		margin: 10px 0 20px;
	}
	&__head{
		margin: 10px 0 15px;
	}
}//.howw

.storry{
	&-1{
		.storry__img{
			max-width: 555px;
		}
	}
	&-2{
		.storry__img{
			max-width: 555px;
		}
	}
	&__full{
		.storry__blck{
			max-width: inherit;
		}
		img{
			display: inline-block;
		}
	}
	&__wrapp{
		align-items: center;
		iframe{
			max-width: 555px;
			// width: auto;
		}
	}
	&__blck{
		padding: 20px 120px 20px 120px;
		max-width: 900px;
	}
	&__txt{
		margin-top: 40px;
	}
	&-1{
		background-color: #EBFEFF;
		.storry__head{
			color: #544EF0;
		}
		.read-mmore{
			background-color: #544EF0;
			&:hover{
				background-color: darken(#544EF0, 10);
			}
		}
	}
	&-2{
		background-color: #fff;
		.storry{
			&__wrapp{
				justify-content: flex-end;
			}
			&__head{
				color: $c-roz;
			}	
		}
		.read-mmore{
			background-color: $c-roz;
			&:hover{
				background-color: darken($c-roz, 10);
			}
		}
	}
	&-3{
		background-color: #F4FFED;
		.storry{
			&__head{
				color: #55D300;
			}	
		}
		.read-mmore{
			background-color: #55D300;
			&:hover{
				background-color: darken(#55D300, 10);
			}
		}
	}
	.read-mmore{
		margin-top: 20px;
	}
	&__full{
		.storry__txt{
			font-size: 24px;
		}
	}
}//.storry

.happyy{
	background-color: #FBFCFC;
	&__list{
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	&__flip{
		transition: 0.6s;
		transform-style: preserve-3d;
		position: relative;
	}
	&__it{
		flex-basis: 32.6%;
		color: #fff;
	  perspective: 1000;
	  width: 370px;
	  height: 323px;
	  margin-bottom: 15px;
		&:hover,
		&.hover{
			.happyy__flip {
				transform: rotateY(180deg);
			}
		}
	}
	&__title{
		color: $c-roz;
		text-align: center;
		margin-bottom: 60px;
	}
	&__name{
		position: absolute;
		bottom: 30px;
		padding: 10px 15px;
		font-size: 24px;
		font-weight: 800;
		width: 100%;
		z-index: 2;
	}
	&__side1{
		text-align: center;
		width: 370px;
	  height: 323px;
	  backface-visibility: hidden;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;
	}
	&__side2{
		padding: 20px;
		width: 370px;
	  height: 323px;
	 	backface-visibility: hidden;
		position: absolute;
		top: 0;
		left: 0;
		transform: rotateY(180deg);
		background: rgba(224,108,160,1);
		background: linear-gradient(135deg, rgba(224,108,160,1) 0%, rgba(236,79,100,1) 100%);
		display: flex;
	  justify-content: center;
	  align-items: center;
		&-txt{
			font-style: italic;
			text-align: center;
			b{
				font-size: 24px;
				font-weight: 800;
				font-style: normal;
				display: block;
				margin-bottom: 30px;	
			}
		}
		&-more{
			margin-top: 15px;
			background-color: #fff;
			color: $c-roz;
			box-shadow: 0 0 3px $c-roz;
			&:hover{
				color: #fff;
			}
		}
	}
	

}//.happyy


.secc{
	background-color: #fff;
	.read-mmore{
		background-color: #fff;
		color: $c-gy;
		border: 2px solid $c-gy;
		&:hover{
			background-color: $c-gy;
			color: #fff;
		}
	}
	&.join{
		color: $c-gy;
		background-image: none;
	}
	.join__inp{
		border-color: $c-roz;
		color: $c-roz;

		&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
		  color: $c-gy;
		}
		&::-moz-placeholder { /* Firefox 19+ */
		  color: $c-gy;
		}
		&:-ms-input-placeholder { /* IE 10+ */
		  color: $c-gy;
		}
		&:-moz-placeholder { /* Firefox 18- */
		  color: $c-gy;
		}
	}	
	.join__subm{
		background-color: $c-roz;
		background-image: url('../img/how_it_works/join2.png');
		border: 2px solid $c-roz;
	}
}


.know-us{
	background-color: #F7FAFA;
	&.secc{
		background-color: #fff;
	}
	&__title{
		text-align: center;
	}
	&__descr{
		text-align: center;
		font-size: 14px;
		margin: 20px 0 30px;
	}
	&__txt{
		font-size: 14px;
	}
	&__list{
		display: flex;
		justify-content: space-between;
	}
	&__blck{
		flex-basis: 24.5%;
		margin: 30px 0;
		text-align: center;
	}
	&__head{
		text-transform: capitalize;
		font-size: 40px;
		font-weight: 800;
		text-align: center;
		a{
			color: #7A7A7A;
			&:hover{
				text-decoration: none;
			}
		}
	}
	&__txt{
		margin: 10px 0 30px;
	}
}//.know-us

.join{
	padding: 30px 0;
	background: url('../img/landing/join-bg.jpg') center no-repeat;
	background-size: cover;
	color: #fff;
	&__blck1{
		font-size: 40px;
		font-weight: 800;
	}
	&__blck2{
		flex: 1;
		text-align: right;
	}
	&__wrap{
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	&__inp{
		height: 60px;
		border: 2px solid #fff;
		font-weight: 600;
		width: 100%;
		max-width: 400px;
		background-color: transparent;
		color: #fff;
		padding: 0 20px;
		vertical-align: middle;
		font-size: 18px;
		&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
		  color: #fff;
		}
		&::-moz-placeholder { /* Firefox 19+ */
		  color: #fff;
		}
		&:-ms-input-placeholder { /* IE 10+ */
		  color: #fff;
		}
		&:-moz-placeholder { /* Firefox 18- */
		  color: #fff;
		}
	}
	&__subm{
		width: 60px;
		height: 60px;
		background: #fff url('../img/landing/join1.png') center no-repeat;
		border: 2px solid #ffffff;
		cursor: pointer;
		vertical-align: middle;
		margin-left: 6px;
		&:hover{
			background-color: darken( #ffffff, 10 );
		}
	}
}//.join

.addr{
	font-family: 'Lato';
	font-weight: bold;
	color: #000;
	img{
		display: inline-block;
	}
	a{
		color: #000;
	}
	&__wrapp{
		border: 1px solid #E3E3E3;
		display: flex;
		justify-content: center;
  	align-items: center;
	}
	&__it{
		flex-basis: 25%;
		text-align: center;
		&.item1{
			font-size: 0;
		}
		.cell-1{
			width: 50px;
			padding-right: 0;
		}
		.cell-2{
			text-align: left;
		}
	}
	&__ico{
		display: inline-block;
		width: 49px;
		height: 49px;
		border: 3px solid $c-roz;
		border-radius: 50px;
		vertical-align: middle;
		// margin-right: 8px;
	}
	&__phone{
		background: url('../img/landing/phone.png') center no-repeat;
	}
	&__street{
		background: url('../img/landing/home.png') center no-repeat;
	}
	&__mail{
		background: url('../img/landing/envelope.png') center no-repeat;
	}
}

.footter{
	background-color: $c-gy;
	color: #fff;
	font-size: 16px;
	&__wrapp{
		display: flex;
		justify-content: space-between;
	}
	a{
		color: #fff;
	}
	&__blck{
		padding: 20px 0;
	}
	&__menu{
		list-style-type: none;
		li{
			display: inline-block;
			margin: 0 10px;
			&:last-child{
				margin: 0;
			}
		}
	}
}