.topp{
	&__jumbo{
		text-align: center;
		color: #fff;
		padding: 170px 0 100px;
		&-head{
			font-weight: 700;
			font-size: 60px;
			text-shadow: 2px 2px 5px $c-gy;
		}
		&-descr{
			font-size: 20px;
		}
	}
}
.overview{
	&__title{
		color: $c-roz;
		text-align: center;
	}
	&__descr{
		text-align: center;
		margin-bottom: 30px;
	}
	&__list{
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		margin-bottom: 100px;
		&-inner{
			position: relative;
			z-index: 9;
		}
		&-it{
			max-width: 370px;
			min-height: 370px;
			width: 100%;
			height: 100%;
			margin-top: 15px;
			color: #fff;
			text-align: center;
			display: flex;
			justify-content: center;
  		align-items: center;
  		flex-direction: column;
  		line-height: 1;
  		padding: 20px;
  		font-size: 20px;
			b{
				font-size: 36px;
				font-weight: 700;
				line-height: 1;
			}
			p:last-child{
				margin-bottom: 0;
			}
			.read-mmore{
				background-color: transparent;
				border: 2px solid #fff;
				padding: 8px 32px;
				&:hover{
					background-color: #fff;
					color: $c-gy;
				} 
			}
		}
	}
	&__txt{
		font-size: 24px;
		text-align: center;
		// margin: 100px 0 0px;
		a{
			font-size: 18px;
			font-weight: 700;
			color: $c-gy;
			text-decoration: underline;	
		}
		
	}
}//.overview