.time-fert{
	&__blck{
		background: transparent;
		.storry__head{
			color: #544EF0;
		}
		.read-mmore{
			border: 2px solid #544EF0;
			color: #544EF0;
		}
	}	
}

.vidd{
	&__list{
		&-head{
			font-size: 30px;
			font-weight: 800;
			margin-bottom: 20px;
		}
		&-it{
			display: flex;
			align-items: center;
			padding-bottom: 30px;
			margin-bottom: 30px;
			border-bottom: 1px solid #E3E3E3;
		}
		&-blck{
			&.blck-1{
				flex: 4;
			}
			&.blck-2{
				flex: 7;
			}
		}
		&-tbl{
			margin-top: 20px;
			.cell-1{
				width: 54px;
				padding-right: 0;
			}
			b{
				font-size: 22px;
				font-weight: 800;
				display: block;
			}
			em{
				font-style: italic;
				display: block;
			}
		}
	}
	&__quality{
		width: 125px;
		height: 125px;
		background: url('../img/time_and_fertility/vidd2.png') center no-repeat;
		display: block;
		position: absolute;
		right: -62.5px;
		top: 50%;
		margin-top: -62.5px;
		z-index: 3;
	}
	&__img{
		&-wrap{
			position: relative;
			display: inline-block;
		}
	}
}