}

h2{
	
}
h3{
	font-weight: bold;	
	font-size: 21px;
}

h5{

}

.icon {
  display: inline-block;
}

.content{
	// font-weight: normal;
	// line-height: 1.2;
	p{
		margin-bottom: 20px;
		line-height: 1.7;
	}
	ul,ol{
		margin-bottom: 20px;
		margin-left: 20px;
	}
	
	li{
		margin-bottom: 15px;
	}


	img{
		margin-bottom: 20px;
	}
	strong{
	}
	iframe{
		width: 100%;
		height: auto;
		min-height: 310px;
		display: block;
		margin: 40px auto;
	}

	table{
		width: 100%;
		margin: 30px 0;
		tr:nth-child(even){
			td{
				background-color: #F6F6F6;
			}
		}
		tr:nth-child(odd){
			td{
				background-color: #ffffff;
			}
		}
		tr:first-child{
			td{
			}
		}
		td{
			text-align: center;
			padding: 15px 10px;
		}
	}
	
}//END.content

.hamburger{
	// padding: 0 ;
	border: 1px solid #fff;
	padding: 10px 10px 3px;
	border-radius: 4px;
	&-inner{
		background-color: #fff;
		width: 24px;
		&:before{
			background-color: #fff;
			width: 24px;
		}
		&:after{
			background-color: #fff;
			width: 24px;	
		}
	}	
	&-box{
		width: 24px;
	}
}

.w-line{
	&:after{
		content: "";
		width: 42px;
		height: 2px;
		background-color: $c-gy;
		display: block;
		margin-bottom: 30px;
		margin-left: auto;
		margin-right: auto;
	}
}

.ov-hidd{
	overflow: hidden;
}

// ADDITIONAL CLASSES

.table-responsive {
  min-height: .01%;
  overflow-x: auto;
}

a{ 
	transition: all .3s ease-out;
	text-decoration: none;
	// color: $c-carr;
	&:hover{
		// color: $c-red;
		text-decoration: underline;
	}
}

button{ 
	transition: all .3s ease-out;
}
input{
	transition: all .3s ease-out;
}


#cboxContent{
	button{
		transition: none;
	}
}


.flexx{
	display: flex;
}
.flexx-wrap{
	flex-wrap: wrap;
}




// HACKS

.tbl{
	display: table;
	width: 100%;
}
.tbl-row{
	display: table-row;
}
.tbl-cell{
	display: table-cell;
	padding-left: 10px;
	padding-right: 10px;
	&:first-child{
		padding-left: 0;
	}
	&:last-child{
		padding-right: 0;
	}
}
.mdl{
	vertical-align: middle;
}
.vat{
	vertical-align: top;
}
.txt-cent{
	text-align: center;
}
.txt-left{
	text-align: left;
}
.txt-rght{
	text-align: right;
}
.fl-lft{
	float:left;
}
.fl-rght{
	float:right;
}
.clearfix:after {
	content: "";
	display: table;
	clear: both;
}
.c:after {
	content: "";
	display: table;
	clear: both;
}
.d-i-b{
	display: inline-block;
}

[class*="kd-col-"] {
	float: left;
	position: relative;
  min-height: 1px;
  padding-left: 10px;
  padding-right: 10px;
}

.kd-row{
	margin-left: -10px;
	margin-right: -10px;
}

.kd-row:before{
  content: " ";
  display: table;
}

.kd-row:after {
	content: "";
	clear: both;
	display: table;
}
.maw-800{
	max-width: 900px;
	margin: 0 auto;
}

.kd-col-1 {width: 8.33%;}
.kd-col-2 {width: 16.66%;}
.kd-col-3 {width: 25%;}
.kd-col-4 {width: 33.33%;}
.kd-col-5 {width: 41.66%;}
.kd-col-6 {width: 50%;}
.kd-col-7 {width: 58.33%;}
.kd-col-8 {width: 66.66%;}
.kd-col-9 {width: 75%;}
.kd-col-10 {width: 83.33%;}
.kd-col-11 {width: 91.66%;}
.kd-col-12 {width: 100%;}

.kd-col-13 {width: 20%;}

//HELPERS
.hidden-d{
	display: none;
}
.shown-d{
	display: block;
}
img{
	display: block;
	max-width: 100%;
	height: auto;
}