.side-menu{
	background-color: #FE6CA3;
	position: absolute;
	top: 0;
	right: 0;
	width: 100%;
	max-width: 500px;
	transition: .5s linear;
	transform: translateX(500px);
	opacity: 0;
	visibility: hidden;
	// transition-delay: .2s;

	&.akktiff{
		transform: translateX(0px);
		opacity: 1;
		visibility: visible;
		// transition-delay: .2s;
	}
	a{
		color: #fff;
	}
	&__nav{
		list-style-type: none;
		text-align: center;
		font-size: 24px;
		margin: 100px 0;
		a{
			display: block;
			padding: 15px 20px;
		}
	}
	&__close{
		width: 70px;
		height: 70px;
		margin: 20px auto;
		border-radius: 50%;
		background: rgba( 255,255,255,0.3 ) url('../img/landing/close.png') center no-repeat;
		cursor: pointer;
		transition: .2s linear;
		transform: rotate(180deg);
		&.akktiff{
			transform: rotate(0deg);

		}
	}
	&__footer{
		display: flex;
		background:  url('../img/landing/wave.png') center no-repeat;
	  // justify-content: center;
	  align-items: center;
		.blck-1{
			flex: 3;
			padding: 15px 5px 15px 25px;
		}
		.blck-2{
			flex: 2;

		}
	}
	&__blck{
		color: #FFC4D9;
		padding: 15px 5px;
	}
}