@media (max-width: 1200px){
	.cont1{
		padding-left: 15px;
		padding-right: 15px;
	}
	.office__wrapp{
		padding-left: 15px;
	}
}

@media (max-width: 1140px){
	.process{
		&__list{
			flex-wrap: wrap;
		}
		&__blck{
			flex-basis: 48%;
			min-height: auto;
		}	
	}
	.office{
		flex-wrap: wrap;
		&__blck{
			flex-basis: 100%;
			&.blck-1{
				order: 2; 
			}
			&.blck-2{
				order: 1;
			}
		}
		&__wrapp{
			max-width: inherit;
			padding: 30px 15px;
		}
	}
}

@media (max-width: 992px){
	.sect-title{
		font-size: 32px;
	}
	.s{
		padding-top: 30px;
		padding-bottom: 30px;
	}

	.physicians{
		.blck2{
			padding: 20px;
		}
		&__txt{
			padding: 0;
		}
		&__img{
			margin-top: 0;
		}	
	} 
}

@media (max-width: 768px){
	.facts{
		&__blck{
			max-width: inherit;
		}	
	}
}

@media (max-width: 749px){
	.process{
		&__blck{
			flex-basis: 100%;
		}	
		&__head{
			margin: 10px 0 20px;
		}
	}
	.colored{
		&__blck{
			width: 100%;
		}
		&__head{
			margin-bottom: 20px;
		}
	}
}

@media (max-width: 550px){
	.footer{
		&__inner{
			text-align: center;
		}
		&__logo{
			margin-bottom: 15px !important;
		}
	}
	.physicians{
		.blck2{
			width: 100%;
			padding-left: 0;
			padding-right: 0;
		}
		.blck1{
			max-width: inherit;
		}
	} 
	.office{
		&__tbl{
			flex-wrap: wrap;
			.celll{
				flex-basis: 100%;
				padding: 15px;
			}
		}
	}	
}

