.gyy{
	.storry__head{
		color: $c-gy;
	}
}

.roz{
	.read-mmore{
		color: $c-roz;
		border: 2px solid $c-roz;
		&:hover{
			color: #fff;
			background-color: $c-roz;
		}
	}		
}

.rozz{
	.vidd__quality{
		background: url('../img/how_it_works/vidd2.png') center no-repeat;
	}
}

.progg{
	&__it{
		min-height: 370px;
		border: 1px solid #E3E3E3;
		box-shadow: 1px 1px 5px $c-gy;
		background-color: #FBFCFC;
		padding: 20px 30px;
		text-align: center;
		margin-bottom: 20px;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		&.firsst{
			justify-content: flex-end;
		}
	}
	&__img{
		margin-bottom: 15px;
		img{
			margin: 0 auto;
		}
	}
	&__head{
		font-size: 22px;
		font-weight: 800;
		color: $c-roz;
	}
	&__txt{
		font-size: 14px;
		margin: 10px 0;
		padding: 0 20px;
	}
	&__counter{
		width: 75px;
		height: 75px;
		color: #fff;
		background-color: $c-gy;
		border-radius: 50%;
		font-weight: 700;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 auto;
		line-height: 1;
	}
}//.progg

.w-lab{
	position: relative;
	span{
		display: block;
		width: 75px;
		height: 75px;
		position: absolute;
		left: -90px;
		top: 50%;
		margin-top: -37.5px;
		background-color: $c-gy;
		color: #fff;
		border-radius: 50%;
		display: flex;
		justify-content: center;
  	align-items: center;
	}
}

.eggs{
	&__it{
		display: flex;
		align-items: center;
		.blck-1{
			flex-basis: 25%;
		}
		.blck-2{
			flex-basis: 75%;
		}
	}
	&__wrap{
		max-width: 800px;
	}
	&__head{
		padding-left: 100px;
		position: relative;
		span{
			width: 75px;
			height: 75px;
			background-color: $c-gy;
			border-radius: 50%;
			color: #fff;
			font-size: 24px;
			font-weight: 800;
			padding: 10px;
			display: flex;
			align-items: center;
			justify-content: center;
			text-align: center;
			position: absolute;
			left: 0px;
			top: 10px;
		}
	}
	&__txt{
		padding-left: 100px;
	}
	&__img{
		margin: 0 auto;
	}
	.even{
		.eggs__wrap{
			float: right;
		}
	}
	&__arr{
		&-fw{
			img{
				margin: 0 auto;
			}
		}
	}
}//.eggs