.roundz{
	text-align: center;
	&__it{
		width: 370px;
		height: 370px;
		border-radius: 50%;
		position: relative;
		overflow: hidden;
		display: inline-block;
		margin: 10px 40px;
	}
	&__head{
		z-index: 3;
		position: absolute;
	  top: 50%;
	  left: 50%;
	  transform: translate(-50%, -50%);
	  color: #fff;
	  text-align: center;
	  font-size: 36px;
	  font-weight: 800;
	  width: 100%;
	}
}

.butt{
	&.blu{
		color: $c-blu ;
		border-color: $c-blu;
		&.prev{
			&:before{
				background: url(../img/get_to_know_us/arr.png) center no-repeat;
			}
		}
		&.next{
			&:after{
				background: url(../img/get_to_know_us/arr-next.png) center no-repeat;
			}
		}
	}
	&.gyy{
		color: $c-gy ;
		border-color: $c-gy;
		&.prev{
			&:before{
				background: url(../img/get_to_know_us/arr-gy.png) center no-repeat;
			}
		}
		&.next{
			&:after{
				background: url(../img/get_to_know_us/arr-next-gy.png) center no-repeat;
			}
		}
	}	
}

.load-more{
	vertical-align: middle;
	font-size: 22px;
	color: $c-gy;
	background-color: transparent;
	border: 3px solid $c-gy;
	border-radius: 4px;
	cursor: pointer;
	font-weight: 700;
	padding: 10px 30px;
	margin: 30px auto;
	display: block;
	&.spin{
		i{
			animation-duration: 1s;
		  animation-name: changewidth;
		  animation-iteration-count: infinite;
    	animation-timing-function: linear;
		}
	}

	i{
		display: inline-block;
		width: 30px;
		height: 30px;
		background: url('../img/get_to_know_us/load-more.png') center no-repeat;
		vertical-align: middle;
	}
	&:hover{
		color: #fff;
		background-color: lighten($c-gy,10);
	}
}

@keyframes changewidth {
  from {
     transform: rotate(0deg);
  }

  to {
     transform: rotate(360deg);
  }
}

.ss{
	padding: 40px 0;
}

.vars{
	&__list{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	&__blck{
		padding: 50px 15px 30px;
		box-shadow: 0 0 10px $c-gy;
		flex-basis: 23.5%;
		background-color: #FBFCFC;
		position: relative;
		margin: 10px 0;
		&.blck-1{
			.vars__line{
				background: linear-gradient(to right, rgba(16,222,233,1) 0%, rgba(81,79,236,1) 100%);
				filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#10dee9', endColorstr='#514fec', GradientType=1 );
			}			
		}
		&.blck-2{
			.vars__line{
				background: linear-gradient(to right, rgba(230,146,164,1) 0%, rgba(196,39,118,1) 100%);
				filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e692a4', endColorstr='#c42776', GradientType=1 );
			}			
		}
		&.blck-3{
			.vars__line{
				background: linear-gradient(to right, rgba(99,16,233,1) 0%, rgba(237,164,242,1) 100%);
				filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#6310e9', endColorstr='#eda4f2', GradientType=1 );
			}			
		}

		&.blck-4{
			.vars__line{
				background: linear-gradient(to right, rgba(0,184,117,1) 0%, rgba(168,236,79,1) 100%);
				filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00b875', endColorstr='#a8ec4f', GradientType=1 );
			}			
		}


		
	}
	&__line{
		position: absolute;
		height: 8px;
		width: 100%;
		top: 0;
		left: 0;
	}
	&__bg{
		width: 153px;
		height: 151px;
