.load-more { vertical-align: middle; font-size: 22px; color: #7A7A7A; background-color: transparent; border: 3px solid #7A7A7A; border-radius: 4px; cursor: pointer; font-weight: 700; padding: 10px 30px; margin: 30px auto; display: block; }

.load-more.spin i { animation-duration: 1s; animation-name: changewidth; animation-iteration-count: infinite; animation-timing-function: linear; }

.load-more i { display: inline-block; width: 30px; height: 30px; background: url("../img/get_to_know_us/load-more.png") center no-repeat; vertical-align: middle; }

.load-more:hover { color: #fff; background-color: #949494; }

@keyframes changewidth { from { transform: rotate(0deg); }
  to { transform: rotate(360deg); } }

.ss { padding: 40px 0; }

.vars__list { display: flex; flex-wrap: wrap; justify-content: space-between; }

.vars__blck { padding: 50px 15px 30px; box-shadow: 0 0 10px #7A7A7A; flex-basis: 23.5%; background-color: #FBFCFC; position: relative; margin: 10px 0; }

.vars__blck.blck-1 .vars__line { background: linear-gradient(to right, #10dee9 0%, #514fec 100%); filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#10dee9', endColorstr='#514fec', GradientType=1 ); }

.vars__blck.blck-2 .vars__line { background: linear-gradient(to right, #e692a4 0%, #c42776 100%); filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e692a4', endColorstr='#c42776', GradientType=1 ); }

.vars__blck.blck-3 .vars__line { background: linear-gradient(to right, #6310e9 0%, #eda4f2 100%); filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#6310e9', endColorstr='#eda4f2', GradientType=1 ); }

.vars__blck.blck-4 .vars__line { background: linear-gradient(to right, #00b875 0%, #a8ec4f 100%); filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00b875', endColorstr='#a8ec4f', GradientType=1 ); }

.vars__line { position: absolute; height: 8px; width: 100%; top: 0; left: 0; }

.vars__bg { width: 153px; height: 151px; display: inline-block; background: url("../img/get_to_know_us/vars-bg.png") center no-repeat; color: #fff; font-size: 36px; font-weight: 900; display: flex; justify-content: center; align-items: center; margin: 0 auto 30px; }

.vars__name { font-size: 28px; font-weight: 900; line-height: 1.2; }

.vars__ico { min-height: 160px; display: flex; justify-content: center; align-items: flex-end; margin-bottom: 30px; }

.vars__ico img { margin: 0 auto; }

.faqs { border-top: 1px solid #7A7A7A; }

.faqs.lasst { border-bottom: 1px solid #7A7A7A; margin-bottom: 50px; }

.faqs__vidd { position: relative; margin-bottom: 65px; }

.faqs__link { position: relative; display: inline-block; }

.faqs__link:hover .faqs__play { background-color: #fff; }

.faqs__link:hover .over__blu2 { opacity: 0; }

.faqs__link:hover .over__blu { opacity: 0; }

.faqs__link:hover .over__red { opacity: 0; }

.faqs__link:hover .over__grn { opacity: 0; }

.faqs__link:hover .over__yw { opacity: 0; }

.faqs__link:hover .over__or { opacity: 0; }

.faqs__play { width: 70px; height: 70px; display: block; z-index: 3; position: absolute; top: 50%; left: 50%; margin-top: -35px; margin-left: -35px; border-radius: 50%; transition: all .3s linear; }

.faqs__play-blu2 { background: rgba(255, 255, 255, 0.5) url("../img/get_to_know_us/play-blu2.png") center no-repeat; }

.faqs__play-red { background: rgba(255, 255, 255, 0.5) url("../img/get_to_know_us/play-red.png") center no-repeat; }

.faqs__play-blu { background: rgba(255, 255, 255, 0.5) url("../img/get_to_know_us/play-blu.png") center no-repeat; }

.faqs__play-grn { background: rgba(255, 255, 255, 0.5) url("../img/get_to_know_us/play-grn.png") center no-repeat; }

.faqs__bg { width: 100px; height: 100px; position: absolute; left: 50%; margin-left: -50px; bottom: -50px; z-index: 3; font-size: 24px; color: #fff; font-weight: 800; display: flex; justify-content: center; align-items: center; }

.faqs__bg-blu2 { background: url("../img/get_to_know_us/faqs_bg_blu2.png") center no-repeat; }

.faqs__bg-red { background: url("../img/get_to_know_us/faqs_bg_red.png") center no-repeat; }

.faqs__bg-blu { background: url("../img/get_to_know_us/faqs_bg_blu.png") center no-repeat; }

.faqs__bg-grn { background: url("../img/get_to_know_us/faqs_bg_grn.png") center no-repeat; }

.faqs__head { font-size: 30px; font-weight: 800; margin-bottom: 15px; }

.faqs__descr { font-size: 18px; font-weight: 500; }

.faqs__tbl { text-align: left; margin: 30px 0; }

.faqs__tbl b { display: block; font-size: 22px; font-weight: 800; }

.faqs__tbl em { font-size: 18px; font-weight: 500; }

.faqs__tbl .cell-1 { width: 64px; }

.faqs-descr { margin-bottom: 40px; }

.faqs-inn { font-size: 22px; font-weight: 500; text-align: left; }

.faqs-inn h3 { background-color: #00C962; text-align: center; color: #fff; border-radius: 4px; padding: 12px; font-size: 24px; font-weight: 800; margin-bottom: 30px; }

.faqs-inn p { text-align: left; padding-left: 30px; background: url("../img/get_to_know_us/gall.png") left 10px no-repeat; }

.faqs-inn ul { list-style-type: none; margin-left: 60px; }

.faqs__ico { display: flex; justify-content: space-between; align-items: center; margin: 80px 0 120px; }

.faqs__ico img { height: 100%; }

.faqs-cost { display: inline-block; margin: 30px 0; }

.faqs-cost .sect1 { background-color: #00C962; color: #fff; font-size: 24px; font-weight: 800; border-radius: 30px 0px 0px 30px; border: 3px solid #00C962; position: relative; width: 139px; }

.faqs-cost .sect2 { border: 3px solid #7A7A7A; border-left: none; border-radius: 0 30px 30px 0; font-size: 24px; font-weight: 700; font-family: 'Lato'; color: #00C962; }

.faqs-cost .tbl-cell { height: 54px; vertical-align: middle; text-align: center; padding: 5px 15px; }

.play__trigg { display: block; width: 88px; height: 88px; position: absolute; top: 50%; left: 50%; margin-top: -44px; margin-left: -44px; background: url("../img/time_and_fertility/play-trigg.png") 0 0 no-repeat; cursor: pointer; z-index: 9; }

.accredit { border-top: 1px solid #e06ca0; }

.accredit__img { min-height: 168px; display: flex; justify-content: center; align-items: center; margin-bottom: 25px; }

.accredit__it { max-width: 280px; margin: 20px auto; }

